import React from 'react';
import axios from 'axios';
import GNav from './GNav';
import { Redirect} from 'react-router-dom';
import Footer from './Footer';
// import loading from '../Assets/Images/126.gif';
// import login from '../Assets/Css/login.css';

class Gproducts extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)
        // console.log(rData[0]);
        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        
        this.state={
            rData,
            loggedIn,
            step: 1,
            insert_id: null,

            //step 1
            id:rData[2],
            shopname: rData[1],
            
            item:'',
            size:'',
            size2:'',
            size3:'',
            size4:'',
            size5:'',
            size6:'',
            price:'',
            price2:'',
            price3:'',
            price4:'',
            price5:'',
            price6:'',
            brand:'',
            desc:'',
            avaliable:'',
            

            //step2
            file: null,
            filename:null,
            
            dataState: false
        }
    }

    nextStep = () => {
        const { step } = this.state;

        // this.setState({
        //     // insert_id: response.data.insertId,
        //     step: step + 1
        // })
  
        if(step === 1){
            // this.setState({ loading: true })
            axios.post("http://localhost:4000/login/g_add1",this.state)
            .then(response => {
                if(response.status === 200){
                  this.setState({
                      insert_id: response.data.insertId,
                      step: step + 1
                  })
                }else{
                  alert("Not submitted..!");
                }
            })
            .catch(err => {
                console.log(err)
            })
            
            
        }
       
      }

      prevStep = () => {
        const { step } = this.state;
        this.setState({
            step: step - 1
        });
      }
      
      handleChange = input =>e => {
        this.setState({[input]: e.target.value});
      }
  
      onFileChange=(e) =>{
        //   console.log([e.target.files[0].name]);
        this.setState({ filename: e.target.files})
        this.setState({ file: [e.target.files[0].name] })
      }
  
      submitHandler = (e) =>{
        e.preventDefault();
       
        axios.post("http://localhost:4000/login/add2_filemv",this.state)
            .then(response => {
                const formData  = new FormData() 
                for (const key of Object.keys(this.state.filename)) {
                    formData.append('filename', this.state.filename[key])
                }
                axios.post("http://localhost:4000/login/add2_fileupload",formData)
                    .then(res => {
                        // console.log(res)
                        if(res.data.status === 1){
                            // this.props.history.push('/products');
                            window.location.reload(true);
                            this.setState({
                                dataState : true
                            })
                           
                        }else{
                            return <div>Something went wrong</div>
                        }
                        setInterval(() => {
                            this.setState({dataState : false})
                        },10000)
                    })
                    .catch(err => {
                        console.log(err)
                    });
        
        })
      }
  
      showStep = () => {
        const { step,insert_id,shopname,id,item,size,size2,size3,size4,size5,size6,price,price2,price3,price4,price5,price6,brand,desc,avaliable,dataState,file } = this.state;
  
        if(step === 1)
            return (<Add1 
                nextStep = {this.nextStep} 
                handleChange = {this.handleChange}
                id={id}
                shopname={shopname} 
                item={item}
                size={size}
                size2={size2}
                size3={size3}
                size4={size4}
                size5={size5}
                size6={size6}
                price={price}
                price2={price2}
                price3={price3}
                price4={price4}
                price5={price5}
                price6={price6}
                brand={brand}
                avaliable={avaliable}
                desc={desc}
                dataState={dataState}
                
            />);
            if(step === 2)
            return (<Add2
                onFileChange = {this.onFileChange}
                insert_id = {insert_id} 
                submitHandler = {this.submitHandler}
                file={file}
                filename={file}
                prevStep = {this.prevStep}
            />);
      }

      render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const { step,dataState } = this.state;
        
            return (
                <div className="addItems">
                    <GNav />
                    
                    <div className="container">
                        <h2>Step {step} of 2.</h2>
                        { dataState ? <div className="alert alert-success">successfully added</div> : null }
                        {this.showStep()}
                    </div>
                    <div>
                       <Footer />
                   </div>
                    
                </div>
            );
        
    }
}

class Add1 extends React.Component{
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }

    render(){
        const{ id,shopname,item,size,size2,size3,size4,size5,size6,price,price2,price3,price4,price5,price6,brand,desc,avaliable,handleChange } = this.props
        return(
            <>
            <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm">
                        <div className="card">
                            <div align="center">
                                <h3><b>Add Product</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form>
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="id" value={id} onChange={handleChange('id')} /><br />
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="shopname" value={shopname} onChange={handleChange('shopname')} /><br />
                                    
                                    <h6><b>Item<span className="required-star">*</span></b></h6>
                                    <input type="text" className="form-control" placeholder="Enter the item" name="item" value={item} onChange={handleChange('item')} required="TRUE"/><br />
                                     <h6><b>plzz enter the price if u entered multiple Quantity</b></h6>  
                                    <h6><b>Quantity<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size" value={size} onChange={handleChange('size')} required="TRUE"/><br />
                                    <h6><b>Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price} onChange={handleChange('price')} required="TRUE"/><br />
                                    <h6>If u had multiple Quantity in same item plzz Enter</h6>
                                    <h6><b>Quantity2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size2" value={size2} onChange={handleChange('size2')} /><br />
                                    <h6><b>Price2<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price2" value={price2} onChange={handleChange('price2')}/><br />
                                    <h6><b>Quantity3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size3" value={size3} onChange={handleChange('size3')} /><br />
                                    <h6><b>Price3<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price3" value={price3} onChange={handleChange('price3')} /><br />
                                    <h6><b>Quantity4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size4" value={size4} onChange={handleChange('size4')} /><br />
                                    <h6><b>Price4<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price4" value={price4} onChange={handleChange('price4')}/><br />
                                    <h6><b>Quantity5<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size5" value={size5} onChange={handleChange('size5')} /><br />
                                    <h6><b>Price5<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price5} onChange={handleChange('price5')} /><br />
                                    <h6><b>Quantity6<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size6" value={size6} onChange={handleChange('size6')} /><br />
                                    <h6><b>Price6<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price6} onChange={handleChange('price6')} /><br />
                                    <h6><b>Brand<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Brand" name="brand" value={brand} onChange={handleChange('brand')} /><br />
                                    <h6><b>Description of item<span className="required-star">*</span></b></h6>
                                        <textarea type="msg" className="form-control" placeholder="Enter the Description" name="desc" value={desc} onChange={handleChange('desc')} /><br />
                                    <h6><b>No of availability item<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the availability" name="avaliable" value={avaliable} onChange={handleChange('avaliable')} required="TRUE"/><br />
                                    <div className="col text-right">
                                        <button className=" btn btn-danger btn-lg" onClick={this.continue}>
                                            Next »
                                        </button>
                                    </div>
                                    
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm"></div>
                </div>
            </div>
            </>
        )
    }
}

class Add2 extends React.Component {
    back = e => {
        e.preventDefault();
        this.props.prevStep();
    }
  
    submit = e =>{
        e.preventDefault();
        this.props.submitHandler(e);
    }
    
    render(){
        // const {  } = this.props;
        return(
            <>
             <div className="container">
                {this.props.insert_id}
                    <div className="row">
                        <div className="col-lg-3 col-md-3 hidden-sm"></div>
                          <div className="col-lg-6 col-md-6 hidden-sm">
                              <div className="card" style={{paddingTop: '2%'}}>
                                  <div className="card-body"> 
                                      <form onSubmit={this.submit} encType="multipart/form-data">
                                        <h6><b>Images of dress<span className="required-star">*</span></b></h6><br />
                                          <input type="file"  name="file" class="form-control"  onChange={this.props.onFileChange} /><br />
                                          <div className="col text-left">
                                              <button className="btn btn-danger btn-lg" onClick={this.back}>
                                                  « Back
                                              </button>
                                          </div>
                                          <div className="col text-right">
                                              <input type="submit" className=" btn btn-success btn-lg"  value="Submit »"/>
                                              {/* <button type="submit" className="btn btn-success" >
                                                  Submit »
                                              </button> */}
                                          </div>
                                      </form>
                                  </div>
                              </div>
                          </div>
                        <div className="col-lg-2 col-md-2 hidden-sm"></div>
                    </div>
                </div>
            </>
        );
    }
}

