import React from 'react';
import CNav from './CNav';
// import axios from 'axios';
import {Redirect,Link} from 'react-router-dom';
import welcome from '../Assets/Images/welcome.gif';
// import nodatafound from '../Assets/Images/nodatafound.png';
import two from '../Assets/Images/2.jpg';
import shop from '../Assets/Images/shop.jpg';
import Footer from './Footer';

class Main_Cdashboard extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            cid:Data[0],
            c_updatestatus:Data[1],
            // loc:'',
            loggedIn,
            // dataState : false,
            // dataStatus:false,
            // data:[],
        }
    }

    // handleChange = (e) => {
    //     e.preventDefault();
    //     this.setState({
    //         [e.target.name] : e.target.value,
    //         data: [],
    //         dataState : false
    //     })
    //     axios.post('http://localhost:4000/customer/search',this.state)
    //         .then(res => {
    //             // console.log(res.data);
    //             this.setState({
    //                 data : res.data,
    //             })
    //             if(res.data.length > 0){
    //                 this.setState({dataState: true})
    //             }else{
    //                 this.setState({ dataStatus: true})
    //             }
                
    //         })
    // }


    // Search =  (e) => {
    //     alert(this.state.loc);
    // }

    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const {  }=this.state;
        return(
            <>
               <CNav />
               { `${this.state.c_updatestatus}` === "0" ? 
               <div>
                   <div className="container">
                   <div className="alert alert-danger">plzz update your profile and login once again place the orders</div>
                       <div className="col-md-4 hidden-sm hidden-xs">
                            <center><Welcome /></center>
                       </div>
                   </div>
                </div> : null }
                { `${this.state.c_updatestatus}` === "1" ? 
               <div>
                        <div className="container-fluid">
                            <center><Welcome /></center><br />
                            <center><b><h4>Select the category of shopping you want to Do</h4></b></center><br />
                            <div className="row">
                                <div className="col-md-4 hidden-sm hidden-xs">
                                    
                                    <div className="card text-black bg-white border-0"  style={{width: '18rem',height: '30rem'}}>
                                        <div className="Image" style={{padding: '3%'}}>
                                            <img src={shop} className="card-image-top" alt={shop} height="300px" width="100%" />
                                        </div>
                                        <div className="card-body text-dark">
                                            <h4>Groceries</h4>
                                            <Link  to="/g_cdashboard" className="btn btn-info btn-lg">Shop</Link>
                                        </div>
                                    </div>
                                </div><br />
                                <div className="col-md-4 hidden-sm hidden-xs">
                                    <div className="card text-black bg-white border-0"  style={{width: '18rem',height: '30rem'}}>
                                        <div className="Image" style={{padding: '3%'}}>
                                            <img src={two} className="card-image-top" alt={two} height="300px" width="100%" />
                                        </div>
                                        <div className="card-body text-dark">
                                            <h4>Dresses Shopping</h4>
                                            <Link  to="/cdashboard_shop" className="btn btn-info btn-lg">Shop</Link>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                   
                </div> : null }
               <div>
                   <Footer />
               </div>
            </>
        )
    }
}
}

class Welcome extends React.Component{
    render(){
        return(
            <div className="welcome">
                <center><img src={welcome} alt="welcome"  width="300px" height="100px" /></center>
            </div>
        )
    }
}

