import React from 'react';
import CNav from './CNav';
import Footer from './Footer';
import { Redirect } from 'react-router-dom';
import axios from 'axios';
// import nodatafound from '../Assets/Images/nodatafound.png';


class GItemData extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            cid:Data[0],
            loggedIn,
            shop: props.match.params.shop,
            id: props.match.params.id,
            item_id:'',
            item:'',
            hno:'',
            area:'',
            city:'',
            c_email:'',
            s_email:'',
            c_name:'',
            c_phone:'',
            s_phone:'',
            colour:'',
            size:'',
            delivery_b:null,
            image:null,
            // search:'',
            // data:[],
            Data:[],
            comment:[],
            price:'',
            payment:'',
            qty:'',
            dataState : false,
            dataStatus:false,
        }
    }

    componentDidMount(){
        axios.post('http://localhost:4000/login/getGroceriesdata',this.state)
        .then(res => {
            // console.log(res);
            if(res.status === 200 && res.data.status === 1){
            this.setState({
                // dataStatus: false,
                Data: res.data.result, 
                item_id:res.data.result[0].id,
                price: res.data.result[0].price,
                item: res.data.result[0].item,
                image: res.data.result.image,
            })
        }else{
            this.setState({
                // dataStatus:false,
            })
            alert("No data Found");
        }
        })
        axios.post('http://localhost:4000/customer',this.state)
            .then(res => {
                // console.log(res)
                if(res.data.status1 === 1  ){
                    this.setState({
                        hno: res.data.result[0].Hno,
                        area: res.data.result[0].area,
                        c_email: res.data.result[0].email,
                        city: res.data.result[0].city,
                        c_name: res.data.result[0].name,
                        c_phone: res.data.result[0].phone,
                    })
                }else{
                    alert("No data Found");
                }
            })
            axios.post('http://localhost:4000/customer/groceriesShopDetails',{shop:this.state.shop})
            .then(res =>{
                if(res.data.status1 === 1  ){
                    this.setState({
                        s_email: res.data.result[0].email,
                        shop_id: res.data.result[0].id,
                        owner: res.data.result[0].owner,
                        s_phone: res.data.result[0].phone,
                        // del1: res.data.result[0].delivery1,
                        // del2: res.data.result[0].delivery1,
                        // del3: res.data.result[0].delivery1,
                        delivery_b:[res.data.result[0].delivery1,res.data.result[0].delivery2,res.data.result[0].delivery3],
                    })
                }else{
                    alert("No data Found");
                }
                // console.log(delivery_b)
            })
            axios.post('http://localhost:4000/customer/commentsDetails',{item_id:this.state.id})
            .then(res=>{
                if(res.data.status === 1){
                    // console.log(res)
                    this.setState({
                        comment:res.data.result,
                        dataStatus:true,
                    })
                }else{
                    // alert("No comments Found");
                }
            })
    }

    handleChange = (e) => {
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value
        })
    }

    submitHandler = (e) => {
        e.preventDefault();
        axios.post("http://localhost:4000/customer/g_order",this.state)
            .then(response => {
                // console.log(response);
                if(response.data.status1 === 1){
                    this.setState({
                        dataState:true,
                    })
                    window.location.reload(true)
                }else{
                    alert("Something went wrong try after some time back to dashboard and order again");
                }
            })
            setInterval(() => {
                this.setState({dataState : false,
                    
                })
            },10000)
    }
   


    render(){
        // if(this.state.dataState === true){
        //     return <Redirect to="/cdashbord" />
        // }
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const { dataState,Data,comment,dataStatus,qty }=this.state;
            // console.log(this.state.)
        return(
            <>
               <CNav /><br />
                    <div className="container-fluid">
                    <div className="row">
                       <div className="col-lg-3 col-md-3 hidden-sm hidden-xs"></div>
                       <div className="col-lg-6 col-md-6 hidden-sm hidden-xs">
                           { dataState ? <div className="alert alert-success">Successfully Ordered</div>:null}
                           {
                               Data.map((data) => (
                                        <div className="card text-black border-0" key={data.id} style={{ padding: '2%'}}>
                                            <img src={'http://localhost:4000/login/image?id='+data.image}  alt={data.image} width="180px" height="200px" />
                                                <div className="card-content text-dark">
                                                    <center><h2 style={{color: 'red'}}><b><em>{ data.item }</em></b></h2></center>
                                                    <div className="container" style={{ paddingTop: '2%'}}>
                                                        <div className="row">
                                                            <div className="col-lg-2 col-md-2 hidden-sm"></div>
                                                            <div className="col-lg-9 col-md-9 hidden-sm">
                                                                <div className="container">
                                                                    <div className="row">
                                                                        <div className="col-lg-4 col-md-4 hidden-sm"></div>
                                                                        <div className="col-lg-4 col-md-4 hidden-sm">
                                                                            <table>
                                                                                <tbody>
                                                                                    
                                                                                    <tr>
                                                                                        <td><p style={{fontSize:'22px'}}><em>Quantity Available:</em></p></td>
                                                                                        <td><p style={{fontSize:'22px'}}>{ data.size},{ data.size2},{ data.size3},{ data.size4},{ data.size5},{ data.size6}</p></td>
                                                                                    </tr>
                                                                                    <tr>
                                                                                        <td><p style={{fontSize:'22px'}}><em>Product Name:</em></p></td>
                                                                                        <td><p style={{fontSize:'22px'}}>{ data.brand}</p></td>
                                                                                    </tr>
                                                                                    <tr>
                                                                                        <td><p style={{fontSize:'22px'}}><em>Description:</em></p></td>
                                                                                        <td><p style={{fontSize:'22px'}}>{ data.description}</p></td>
                                                                                    </tr>
                                                                                    <tr>
                                                                                        <td><p style={{fontSize:'22px'}}><em> price:</em></p></td>
                                                                                        <td><p style={{fontSize:'22px'}}>Rs.{ data.price}</p></td>
                                                                                    </tr>
                                                                                </tbody>
                                                                            </table>
                                                                        </div>
                                                                        <div className="col-lg-4 col-md-4 hidden-sm hidden-xs"></div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-1 col-md-1 hidden-sm hidden-xs"></div>
                                                        </div>
                                                    </div>
                                                    <div className="container">
                                                        <div className="row">
                                                            <div className="col-lg-3 col-md-3 hidden-sm hidden-xs"></div>
                                                            <div className="col-lg-6 col-md-6 hidden-sm hidden-xs">
                                                                <form onSubmit={this.submitHandler}>
                                                                    <b>Select the Quantity</b>
                                                                    <select className="form-control" onChange={this.handleChange} name="size">
                                                                        <option value="">--Select the Quantity---</option>
                                                                        <option value={`${data.size},${data.price}`}>{data.size}-{data.price}</option>
                                                                        <option value={`${data.size2},${data.price2}`}>{data.size2}-{data.price2}</option>
                                                                        <option value={`${data.size3},${data.price3}`}>{data.size3}-{data.price3}</option>
                                                                        <option value={`${data.size4},${data.price4}`}>{data.size4}-{data.price4}</option>
                                                                        <option value={`${data.size5},${data.price5}`}>{data.size5}-{data.price5}</option>
                                                                        <option value={`${data.size6},${data.price6}`}>{data.size6}-{data.price6}</option>
                                                                    </select><br />
                                                                    <b>Select the Quanity is manual if above is empty</b>
                                                                    <input type="text" placeholder="Enter the quantity with kg or packs" className="form-control" name="qty" value={qty} onChange={this.handleChange} /><br />
                                                                    <b>Select the Payment Gateway</b>
                                                                    <select className="form-control" onChange={this.handleChange} name="payment" required>
                                                                        <option value="">--Select the Payment Gateway---</option>
                                                                        <option value="cash on delivery">Cash On Delivery</option>
                                                                    </select><br />
                                                                    <button type="submit" className="btn btn-dark btn-lg">Buy Now</button>
                                                                </form><br />
                                                                <div className="container">
                                                                    {dataStatus ? <div>
                                                                    {   
                                                                        comment.map((data)=>(
                                                                            <div key={data.id}>
                                                                                <h3>Comments</h3><hr />
                                                                                    <b>Description</b>:<p style={{fontSize:'22px'}}><em>{data.description}</em></p>
                                                                                    <b>Name</b>:<p style={{fontSize:'22px'}}><em>{data.name}</em></p>
                                                                                    {/* <b>Rating</b>:<p style={{fontSize:'22px'}}><em>{data.rating} Stars</em></p> */}
                                                                                    <b>Rating</b>:
                                                                                    {`${data.rating}` === '1'? <div><span><i className="fa fa-star-o"></i></span></div>:null}
                                                                                    {`${data.rating}` === '2'? <div>
                                                                                        <span>
                                                                                            <i className="fa fa-star-o"></i>
                                                                                            <i className="fa fa-star-o"></i>
                                                                                            </span></div>:null}
                                                                                    {`${data.rating}` === '3'? <div>
                                                                                        <span><i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        </span></div>:null}
                                                                                    {`${data.rating}` === '4'? <div><span>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        </span></div>:null}
                                                                                    {`${data.rating}` === '5'? <div><span>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        <i className="fa fa-star-o"></i>
                                                                                        </span></div>:null}
                                                                            </div>
                                                                            ))
                                                                    }
                                                                    </div> : null}
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-3 col-md-3 hidden-sm hidden-xs"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                        </div>
                               ))
                           }
                       </div>
                       <div className="col-lg-3 col-md-3 hidden-sm"></div>
                   </div>
                    </div>
                    <div>
                        <Footer />
                    </div>
            </>
        )
    }
}
}

