import React from 'react';
// import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router,Route,Switch } from 'react-router-dom';
import Home from './Components/Home';
import Login from './Components/Login'
import G_login from './Components/G_login'
import Admin from './Components/Admin'
import A_dashboard from './Components/A_dashboard'
import Customer from './Components/Customer'
import Register from './Components/Register';
import G_register from './Components/G_register';
import SDashboard from './Components/SDashboard';
import Gdashboard from './Components/Gdashboard';
import Addproduct from './Components/AddProduct';
import Gproducts from './Components/Gproducts';
import Logout from './Components/Logout';
import Updateprofile from './Components/Updateprofile';
import G_Updateprofile from './Components/G_Updateprofile';
import UpdateDelivery from './Components/UpdateDelivery';
import G_UpdateDelivery from './Components/G_UpdateDelivery';
import Aprooval from './Components/Aprooval';
import Edit from './Components/Edit';
import GEdit from './Components/GEdit';
import ApprovedShops from './Components/ApprovedShops';
import C_register from './Components/C_register';
import C_dashboard from './Components/C_dashboard';
import Main_Cdashboard from './Components/Main_Cdashboard';
import G_Cdashboard from './Components/G_Cdashboard';
import C_update from './Components/C_update';
import C_updateDetails from './Components/C_updateDetails';
import Shops from './Components/Shops';
import GShops from './Components/GShops';
import Item from './Components/Item';
import GItem from './Components/GItem';
import ItemData from './Components/ItemData';
import GItemData from './Components/GItemData';
import Order from './Components/Order';
import S_order from './Components/S_order';
import G_order from './Components/G_order';
import S_orderHistory from './Components/S_orderHistory';
import G_orderHistory from './Components/G_orderHistory';
import OrderHistory from './Components/OrderHistory';
import F_pwdPage from './Components/F_pwdPage';
import SF_pwd from './Components/SF_pwd';
import GF_pwd from './Components/GF_pwd';
import Item1 from './Components/Item1';
import G_Item1 from './Components/G_Item11';
import Item2 from './Components/Item2';
import Item3 from './Components/Item3';
import Contact from './Components/Contact';



function App() {
  return(
    <div className="App">
      <Router>
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/login1" exact component={Login} />
            <Route path="/groceries" exact component={G_login} />
            <Route path="/admin" exact component={Admin} />
            <Route path="/aprooval" exact component={Aprooval} />
            <Route path="/aprooved_shops" exact component={ApprovedShops} />
            <Route path="/customer1" exact component={Customer} />
            <Route path="/cdashboard_shop" exact component={C_dashboard} />
            <Route path="/cdashboard" exact component={Main_Cdashboard} />
            <Route path="/g_cdashboard" exact component={G_Cdashboard} />
            <Route path="/c_updatedetails" exact component={C_updateDetails} />
            <Route path="/c_update" exact component={C_update} />
            <Route path="/a_dashboard" exact component={A_dashboard} />
            <Route path="/sdashboard" exact  component={SDashboard} />
            <Route path="/gdashboard" exact  component={Gdashboard} />
            <Route path="/register" exact component={Register} />
            <Route path="/g_register" exact component={G_register} />
            <Route path="/c_register" exact component={C_register} />
            <Route path="/products" exact component={Addproduct} />
            <Route path="/g_products" exact component={Gproducts} />
            <Route path="/update" exact component={Updateprofile} />
            <Route path="/g_update" exact component={G_Updateprofile} />
            <Route path="/updateDelivery" exact component={UpdateDelivery} />
            <Route path="/g_updateDelivery" exact component={G_UpdateDelivery} />
            <Route path="/shops/:loc" exact component={Shops} />
            <Route path="/g_shops/:loc" exact component={GShops} />
            <Route path="/items/:shop" exact component={Item} />
            <Route path="/g_items/:shop" exact component={GItem} />
            <Route path="/item1/:cat/:shop" exact component={Item1} />
            <Route path="/groceriesitem1/:item/:shop" exact component={G_Item1} />
            <Route path="/item2/:cat/:shop/:item" exact component={Item2} />
            <Route path="/item3/:cat/:shop/:item/:size" exact component={Item3} />
            <Route path="/itemsData/:id/:shop" exact component={ItemData} />
            <Route path="/g_itemsData/:id/:shop" exact component={GItemData} />
            <Route path="/edit/:id" exact component={Edit} />
            <Route path="/g_edit/:id" exact component={GEdit} />
            <Route path="/orders" exact component={Order} />
            <Route path="/s_orders" exact component={S_order} />
            <Route path="/g_orders" exact component={G_order} />
            <Route path="/s_orderhistory" exact component={S_orderHistory} />
            <Route path="/g_orderhistory" exact component={G_orderHistory} />
            <Route path="/orderhistory" exact component={OrderHistory} />
            <Route path="/f_pwdPage" exact component={F_pwdPage} />
            <Route path="/f_pwd" exact component={SF_pwd} />
            <Route path="/Gf_pwd" exact component={GF_pwd} />
            <Route path="/contact" exact component={Contact} />
            <Route path="/logout" component={Logout} />
          </Switch>
       </Router>
    </div>
  );
}

export default App;
