import React from 'react';
import { Component } from 'react';
import {Link} from 'react-router-dom';
// import Styles from '../Styles/Header.css'


class MainNav extends Component{

    render(){
        return(
            <div className="HeaderClass">
            <nav className="navbar fixed-top navbar-expand-lg navbar-light bg-light">
                <Link className="navbar-brand" to="/"><h4 style={{color:'#e40c2b'}}><b><em>OnlyShopping</em></b></h4></Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse " id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle active" to="" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <b style={{color:'#fb9039'}}><em>Register</em></b>
                            </Link>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                            <Link className="dropdown-item" to="/register"><b><em>DressShop Register</em></b></Link>
                            <Link className="dropdown-item" to="/g_register"><b><em>GrocieresShop Register</em></b></Link>
                            <Link className="dropdown-item" to="/c_register" ><b><em>Customer Register</em></b></Link>
                            <div className="dropdown-divider"></div>
                            <Link className="dropdown-item" to="">Something else here</Link>
                            </div>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle active" to="" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <b style={{color:'#fb9039'}}><em>Log IN</em></b>
                            </Link>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                            <Link className="dropdown-item" to="/login1"><b><em>Shop Login</em></b></Link>
                            <Link className="dropdown-item" to="/groceries"><b><em>Groceries Login</em></b></Link>
                            <Link className="dropdown-item" to="/customer1" ><b><em>Customer Login</em></b></Link>
                            </div>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link active"  to="/contact"><b style={{color:'#e40c2b'}}><em>Contacts Us/Report</em></b></Link>
                        </li>
                    </ul>
                    
                </div>
            </nav> <br /><br />  
        </div>
        );
    }
}


export default MainNav;