import React,{Component} from 'react';
import Nav from './Nav';
import axios from 'axios';
import {Redirect,Link} from 'react-router-dom';
import loading from '../Assets/Images/spinner-loader-animation.gif';
import welcome from '../Assets/Images/welcome.gif';
import Footer from './Footer';
//Bootstrap and jQuery libraries
// import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery'; 

// const $  = require( 'jquery' );
// const dt = require( 'datatables.net' )( window, $ );

class SDashboard extends Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        let Data = JSON.parse(token)
        // console.log(JSON.parse(token))
        // console.log(Data)
        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            Data1:Data[0],
            shop:Data[1],
            update:Data[3],
            loggedIn,
            Details: [],
            currentPage: 1,
            DetailsperPage: 1,
           loading: false,
           dataStatus:true,

        }
       
        // console.log(this.state.Data1)
    }


    componentDidMount(){
        axios.post('http://localhost:4000/login/getDbdata',this.state)
        .then(res => {
            // console.log(res.data);
            if(res.data.status===1){
            this.setState({
                dataStatus: false,
                Details: res.data.result, 
            })
        }else{
            alert("no data found!");
        }
        })
        setInterval(() => {
            this.setState({
                dataStatus:false
            })
        },10000)
        
    }

    componentDidUpdate(){

        // componentDidMount() {

            //initialize datatable
            $(document).ready(function () {
                $('#example').DataTable();
            });
        //  }
    }

    delete=(e) =>{
        e.preventDefault();
        this.setState({ loading: true })
        axios.post("http://localhost:4000/login/delete",{id:+e.target.value,shop:this.state.shop})
            .then(response => {
                // console.log(response)
                if(response.data.status === 1){
                    window.location.reload(true)
                }else{
                    this.props.history.push('/')
                }
                
            })
            .catch(err => {
                console.log(err)
            })
    }

    render(){
        // if(this.state.edit){
        //     return <Redirect to="/edit" />
        // }
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
        // const { Data1,update } = this.state
        // console.log(Data1);
        const{ Details,loading,dataStatus }=this.state;
        return(
            <div className="home">
                {/* <Nav /> */}
                {  `${this.state.Data1}` === "0" ? 
                    <div>
                        <div className="HeaderClass">
                            <nav className="navbar fixed-top navbar-expand-md navbar-light bg-light">
                                <Link className="navbar-brand" to="/sdashboard"><h4 style={{color:'#e40c2b'}}><b><em>Home</em></b></h4></Link>
                                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon"></span>
                                </button>
                                <div className="collapse navbar-collapse " id="navbarSupportedContent">
                                    <ul className="navbar-nav ml-auto">
                                        <li className="nav-item">
                                            <ul className="nav-link btn btn-danger btn-sm" style={{ color: 'white' }}>Inactive</ul>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link"  to="/logout" >
                                            <h4 style={{color:'#e40c2b'}}><b><em>Logout</em></b></h4>
                                            </Link>
                                        </li>
                                    </ul>
                                    
                                </div>
                            </nav>    
                        </div><br /><br /><br />
                        <div className="alert alert-info">Wait 24hrs for Admin Aprroval</div>
                    </div> : null }
                { `${this.state.Data1}` === "1" ? 
                <div>
                    <Nav />
                
                    { `${this.state.update}` === "1" ? <div className="alert alert-danger">plzz update your profile and login once again</div> : null }
                    <div className="container">
                        <Welcome /><br />
                        <div className="row">
                            <div className="col-lg-1 col-md-1 hidden-sm hidden-xs"></div>
                            {dataStatus ? <Loading /> :
                            <div className="col-lg-10 col-md-10 hidden-sm hidden-xs"> 
                            {loading ? <Loading /> :
                                <div className="card">
                                    <div className="card-body">
                                        <div className="table-responsive">
                                            <table className="table  table-hover" id="example" border="2px">
                                                <thead className="thead-dark">
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Image</th>
                                                        <th>Category</th>
                                                        <th>Item Type</th>
                                                        <th>Item</th>
                                                        <th>Size</th>
                                                        <th>Size2</th>
                                                        <th>Size3</th>
                                                        <th>Size4</th>
                                                        <th>Size5</th>
                                                        <th>Size6</th>
                                                        <th>suited for year</th>
                                                        <th>colour</th>
                                                        <th>colour2</th>
                                                        <th>colour3</th>
                                                        <th>colour4</th>
                                                        <th>Brand</th>
                                                        <th>Price</th>
                                                        <th>Avaliable</th>
                                                        <th>Edit</th>
                                                        <th>Delete</th>
                                                    </tr>
                                                </thead>
                                                <tbody className="tbody-light">
                                                { Details.map((data) => (
                                                        <tr key={data}>
                                                            <td>{data.id}</td>
                                                            <td><img src={'http://localhost:4000/login/image?id='+data.image1} alt={data.image1} height="100px" width="100px" /></td>
                                                            <td>{data.category}</td>
                                                            <td>{data.itemtype}</td>
                                                            <td>{data.item}</td>
                                                            <td>{data.size}</td>
                                                            <td>{data.size2}</td>
                                                            <td>{data.size3}</td>
                                                            <td>{data.size4}</td>
                                                            <td>{data.size5}</td>
                                                            <td>{data.size6}</td>
                                                            <td>{data.suited_yr}</td>
                                                            <td>{data.colour}</td>
                                                            <td>{data.colour2}</td>
                                                            <td>{data.colour3}</td>
                                                            <td>{data.colour4}</td>
                                                            <td>{data.brand}</td>
                                                            <td>{data.price}</td>
                                                            <td>{data.avaliable}</td> 
                                                            <td><Link  to={'/edit/'+data.id}  class="btn btn-success"  >Edit</Link></td>   
                                                            <td><button type="button" class="btn btn-danger" value={data.id}  onClick={this.delete} >Delete</button></td>   
                                                        </tr>
                                                ))}  
                                                </tbody>
                                            </table>
                                            
                                        </div>
                                    </div>
                                </div>
                            }
                            </div>
                        }
                        <div className="col-lg-1 col-md-1 hidden-sm hidden-xs"></div>
                    </div>
                </div>
                </div> : null }
                <div>
                   <Footer />
                </div>
            </div>
        )
        }
    }
}


class Loading extends Component{
    render(){
        return(
            <div className="loading">
                <center><img src={loading} alt="loading" width="200px" height="400px"/></center>
            </div>
        )
    }
}

class Welcome extends Component{
    render(){
        return(
            <div className="welcome">
                <center><img src={welcome} alt="welcome"  width="300px" height="100px" /></center>
            </div>
        )
    }
}

export default SDashboard;