import React from 'react';
import axios from 'axios';
import GNav from './GNav';
import { Redirect} from 'react-router-dom';
import Footer from './Footer';

class GEdit extends React.Component{
    constructor(props){
        super(props)

        const token = localStorage.getItem("token")
        let rData = JSON.parse(token)
        // console.log(rData[0]);
        let loggedIn = true
        if(token == null){
            loggedIn = false
        }
        
        this.state={
            rData,
            loggedIn,
            pid: props.match.params.id,
            shopname: rData[1],
            

            //step 
            id:'',
            shop:'',
            item:'',
            size:'',
            size2:'',
            size3:'',
            size4:'',
            size5:'',
            size6:'',
            price:'',
            price2:'',
            price3:'',
            price4:'',
            price5:'',
            price6:'',
            brand:'',
            desc:'',
            avaliable:'',
            
            dataState: false
        }
    }

    componentDidMount(){
        axios.post('http://localhost:4000/login/getGroceriesdata',{shop:this.state.shopname,id:this.state.pid})
        .then(res => {
            // console.log(res);
            if(res.status === 200 && res.data.status === 1){
            this.setState({
                id: res.data.result[0].id,
                shop:res.data.result[0].shopname,
                item:res.data.result[0].item,
                size:res.data.result[0].size,
                size2:res.data.result[0].size2,
                size3:res.data.result[0].size3,
                size4:res.data.result[0].size4,
                size5:res.data.result[0].size5,
                size6:res.data.result[0].size6,
                price:res.data.result[0].price,
                price2:res.data.result[0].price2,
                price3:res.data.result[0].price3,
                price4:res.data.result[0].price4,
                price5:res.data.result[0].price5,
                price6:res.data.result[0].price6,
                brand:res.data.result[0].brand,
                desc:res.data.result[0].description,
                avaliable:res.data.result[0].avaliable,
            })
        }else{
            alert("something went wrong");
        }
        })
    }

    handleChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }

    submitHandler=(e) =>{
        e.preventDefault();
        axios.post("http://localhost:4000/login/g_edit",this.state)
        .then(response => {
            if(response.status === 200){
              this.setState({
                 dataState: true
              })
            }else{
              alert("Not submitted..!");
            }
        })
        .catch(err => {
            console.log(err)
        })
        setInterval(() => {
            this.setState({dataState : false,
            })
        },10000)
    }
    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }
        const { shopname,id,item,size,size2,size3,size4,size5,size6,price,price2,price3,price4,price5,price6,brand,desc,avaliable,dataState } = this.state;
        return(
            <>
                <GNav /><br />
                <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-3 hidden-sm hidden-xs"></div>
                    <div className="col-lg-6 col-md-6 hidden-sm hidden-xs">
                        <div className="card">
                            { dataState ? <div className="alert alert-success">Successfully submited</div> : null}
                            <div align="center">
                                <h3><b>Edit Product</b></h3><hr />
                            </div>
                            <div className="card-body">
                                <form onSubmit={this.submitHandler}>
                                   
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="id" value={id} onChange={this.handleChange} /><br />
                                <input type="hidden" className="form-control" placeholder="Enter the Size" name="shopname" value={shopname} onChange={this.handleChange} /><br />
                                <h6><b>Item<span className="required-star">*</span></b></h6>
                                    <input type="text" className="form-control" placeholder="Enter the item" name="item" value={item} onChange={this.handleChange} required="TRUE"/><br />
                                     <h6><b>plzz enter the price if u entered multiple Quantity</b></h6>  
                                    <h6><b>Quantity<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size" value={size} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Price<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price} onChange={this.handleChange} required="TRUE"/><br />
                                    <h6><b>Quantity2<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size2" value={size2} onChange={this.handleChange} /><br />
                                    <h6><b>Price2<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price2" value={price2} onChange={this.handleChange}/><br />
                                    <h6><b>Quantity3<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size3" value={size3} onChange={this.handleChange} /><br />
                                    <h6><b>Price3<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price3" value={price3} onChange={this.handleChange} /><br />
                                    <h6><b>Quantity4<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size4" value={size4} onChange={this.handleChange} /><br />
                                    <h6><b>Price4<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price4" value={price4} onChange={this.handleChange}/><br />
                                    <h6><b>Quantity5<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size5" value={size5} onChange={this.handleChange} /><br />
                                    <h6><b>Price5<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price5} onChange={this.handleChange} /><br />
                                    <h6><b>Quantity6<span className="required-star">(optional)</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Quantity" name="size6" value={size6} onChange={this.handleChange} /><br />
                                    <h6><b>Price6<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Price" name="price" value={price6} onChange={this.handleChange} /><br />
                                    <h6><b>Brand<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the Brand" name="brand" value={brand} onChange={this.handleChange} /><br />
                                    <h6><b>Description of item<span className="required-star">*</span></b></h6>
                                        <textarea type="msg" className="form-control" placeholder="Enter the Description" name="desc" value={desc} onChange={this.handleChange} /><br />
                                    <h6><b>No of availability item<span className="required-star">*</span></b></h6>
                                        <input type="text" className="form-control" placeholder="Enter the availability" name="avaliable" value={avaliable} onChange={this.handleChange} required="TRUE"/><br />
                                    <div className="col text-right">
                                        <button type="submit" className=" btn btn-danger btn-lg">
                                            Edit »
                                        </button>
                                    </div>
                                    
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-3 hidden-sm hidden-xs"></div>
                </div>
            </div>
            <div>
                <Footer />
            </div>
            </>
        )
    }
}

export default GEdit;