import React from 'react';
import CNav from './CNav';
import Footer from './Footer';
import { Redirect,Link } from 'react-router-dom';
import axios from 'axios';
// import nodatafound from '../Assets/Images/nodatafound.png';


class GItem extends React.Component{
    constructor(props){
        super(props)
        const token = localStorage.getItem("token")
        // let Data = JSON.parse(token)

        let loggedIn = false
        if(token == null){
            loggedIn = false
        }
        this.state={
            loggedIn,
            shop: props.match.params.shop,
            search:'',
            data:[],
            Data:[],
            // Data1:[],
            dataState : false,
            dataStatus:false,
        }
    }

    componentDidMount(){
        axios.post('http://localhost:4000/login/getGroceriesDbdata',this.state)
        .then(res => {
            // console.log(res);
            if(res.data.status === 1){
            this.setState({
                // dataStatus: false,
                Data: res.data.result, 
                // Data1:res.data.result
            })
        }else{
            this.setState({
                // dataStatus:false,
            })
            alert("No data Found");
        }
        })
    }

    handleChange = (e) => {
        e.preventDefault();
        this.setState({
            [e.target.name] : e.target.value,
            
            data: [],
            dataState : false
        })
        // axios.post('http://localhost:4000/customer/groceriesSearch1',this.state)
        //     .then(res => {
        //         // console.log(res.data);
        //         this.setState({
        //             data : res.data,
        //         })
        //         if(res.data.length > 0){
        //             this.setState({dataState: true})
        //         }else{
        //             this.setState({ dataStatus: true})
        //         }
                
        //     })
    }

    Search =  (e) => {
        alert(this.state.search);
    }


    render(){
        if(this.state.loggedIn === true){
            return <Redirect to="/" />
        }else{
            const { search,Data }=this.state;
        return(
            <>
               <CNav /><br />
                    <div className="container">
                        <div className="row">
                        <div className="col-lg-4 col-md-4 hidden-sm hidden-xs"></div>
                        <div className="col-lg-4 col-md-4 hidden-sm hidden-xs">
                            <div align="center">
                                <form onSubmit={this.Search} autoComplete="off">
                                    <h6><b><em>Search the product u want  *</em></b></h6>
                                    <input type="text" className="form-control" placeholder="Enter your product" name="search" value={search}  onChange={this.handleChange}  /><br />
                                    {/* {dataState ? <div id="searchData">
                                        <ul className="list-group">
                                            {
                                                data.map((items) =>(
                                                    <a href={`/groceriesitem1/${items.item}/${items.shop_name}`} key={items.id} ><li className="list-group-item searchIndivi"><div className="" >{items.item} </div></li></a> 
                                                ))
                                            }
                                        </ul>
                                    </div> : null}
                                    {dataStatus ? <NoData /> : null} */}
                                    {/* <button type="submit" className="btn btn-success btn-lg">Submit</button> */}
                                </form>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 hidden-sm hidden-xs"></div>
                        </div>
                    </div>
                    <div className="container">
                    <div className="row justify-content-center">
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                       {/* <div className="col-lg-8 col-md-8 hidden-sm"> */}
                           {
                               Data.filter(search => search.item.includes(`${this.state.search}`)).map((data) => (
                                   <div className="col-md-4 col-12 hidden-xs">
                                        <div className="card text-black bg-white border-0" key={data.id} style={{width: '18rem',height: '28rem'}}>
                                            <div className="Image" style={{padding: '3%'}}>
                                                <img src={'http://localhost:4000/login/image?id='+data.image} className="card-image-top" alt={data.image} height="180px" width="100%" />
                                            </div>
                                            <div className="card-body text-dark">
                                                <center><h2 className="card-title" style={{color: 'red'}}><b><em>{ data.item }</em></b></h2></center>
                                                <h6>for quantity change plzz enter the button</h6>
                                                <p  style={{fontSize:'22px'}}>Rs.{ data.price}</p>
                                                <Link type="button" to={`/g_itemsData/${data.id}/${data.shop_name}`} className="btn btn-dark btn-lg">View Item</Link>
                                            </div>
                                        </div><br />
                                    </div>
                               ))
                           }
                           
                           {/* { `${this.state.search}`==='' ? 
                            <div>
                                {
                               Data1.map((data) => (
                                   <div className="col-md-4 col-12 hidden-xs">
                                        <div className="card text-black bg-white border-0" key={data.id} style={{width: '18rem',height: '28rem'}}>
                                            <div className="Image" style={{padding: '3%'}}>
                                                <img src={'http://localhost:4000/images/'+data.image} className="card-image-top" alt={data.image} height="180px" width="100%" />
                                            </div>
                                            <div className="card-body text-dark">
                                                <center><h2 className="card-title" style={{color: 'red'}}><b><em>{ data.item }</em></b></h2></center>
                                                <h6>for quantity change plzz enter the button</h6>
                                                <p  style={{fontSize:'22px'}}>Rs.{ data.price}</p>
                                                <Link type="button" to={`/g_itemsData/${data.id}/${data.shop_name}`} className="btn btn-dark btn-lg">View Item</Link>
                                            </div>
                                        </div><br />
                                    </div>
                               ))
                           }
                            </div>
                           : null} */}
                       {/* </div> */}
                       {/* <div className="col-lg-2 col-md-2 hidden-sm"></div> */}
                   </div>
                    </div>
                    <div>
                        <Footer />
                    </div>
            </>
        )
    }
}
}

